html,
body {
  height: 100%;
  font-family: "Segoe UI", "\5FAE\8EDF\6B63\9ED1\9AD4", sans-serif;
  font-weight: bold;
}

#root {
  height: 100%;
}

@font-face {
  font-family: "icomoon";
  src: url(/weather-forecast/static/media/icomoon.f4ae08df.eot);
  src: url(/weather-forecast/static/media/icomoon.f4ae08df.eot) format("embedded-opentype"),
    url(/weather-forecast/static/media/icomoon.9e94e87c.ttf) format("truetype"),
    url(/weather-forecast/static/media/icomoon.fcf32c41.woff) format("woff"),
    url(/weather-forecast/static/media/icomoon.36ba6e52.svg) format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.icon-sun:before {
  content: "\E902";
}
.icon-moon:before {
  content: "\E903";
}
.icon-wind:before {
  content: "\E901";
}
.icon-cloudy:before {
  content: "\E904";
}
.icon-cloud:before {
  content: "\E905";
}
.icon-weather1:before {
  content: "\E900";
}
.icon-rainy:before {
  content: "\E906";
}
.icon-snowy:before {
  content: "\E907";
}
.icon-weather:before {
  content: "\E908";
}
.icon-cloudy1:before {
  content: "\E909";
}

.background-image::after {
  content: "";
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
  background-image: url(/weather-forecast/static/media/bk.cb93c15e.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

/* Hide scroll bar, but while still being able to scroll */
.carousel-wrap {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
}
.carousel-wrap::-webkit-scrollbar {
  width: 0;
  height: 0;
}

/* Workaround for flex bug :(
 * minimum content sizing of flex items not honored 
 * https://github.com/philipwalton/flexbugs#flexbug-1 */
.flex-minimum-content-sizing {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-basis: auto;
          flex-basis: auto;
}


/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
